import React from "react";
import { useFirebaseNode } from "../hooks/useFirebaseNode";

import Layout from "../components/layout"
import CustomDropdownComponent from "../components/CustomDropdownComponent";

import Alert from 'react-bootstrap/Alert'
import ListGroup from 'react-bootstrap/ListGroup'

export default function SearchWorkBooks() {
    const { data: usersCraft, loading, error } = useFirebaseNode('usersCraft');
    const [user, setUser] = React.useState(null);

    if (!!loading || !usersCraft) { return <Layout><div>...</div></Layout> }

    if (!!error) { return <Layout><div>:-(</div></Layout> }

    return <Layout>
        <CustomDropdownComponent dropDownItems={Object.keys(usersCraft)} selectedItem={setUser} />
        <UserWorkbook user={user} userData={usersCraft[user]} />
    </Layout>
}

function UserWorkbook(props) {
    let data = !!props?.userData?.posts ? Object.keys(props.userData.posts).map(item => { return props.userData.posts[item] }) : [];
    let avatarUrls = [...new Set(data.filter(item => !!item?.avatarUrl).map(filteredItem => filteredItem.avatarUrl))];
    let users = [...new Set(data.filter(item => !!item?.user).map(filteredItem => filteredItem.user))];
   
    const uniqueThemes = !!data ? [...new Set(data.map(item => item.theme))] : []; // [ 'A', 'B']



    return <> <div className="media border p-3 mb-5">

        {avatarUrls.map(item =>
            <img key={item}
                src={item} alt=""
                className="m-3"
                style={{
                    verticalAlign: 'middle',
                    width: '60px',
                    height: '60px',
                    borderRadius: '50%',
                    filter: 'grayscale(100%)',
                    objectFit: 'cover'
                }} />
        )}

        <div className="media-body">
            <p>Рабочая тетрадь {props.user}</p>
            {users.map(item => <p key={item}>{item}</p>)}
        </div>
    </div>
        <div>{!!uniqueThemes && uniqueThemes.map(theme =>
            <div key={theme}>
                <Alert variant='primary'>
                    {theme}
                </Alert>
                <ListGroup variant="flush">
        {data.filter(quiz => quiz.theme === theme).map((quiz, index) =>
        <ListGroup.Item key={index}>
            Задача: {!!quiz?.title ? quiz.title : ''} Дата/время: {!!quiz?.date ? quiz.date : ''} Id={!!quiz?.id ? <small>{quiz.id}</small>: ''}
         </ListGroup.Item>)}
                </ListGroup>
            </div>
        )}
        </div>
    </>
}