import React from "react";


import FormControl from 'react-bootstrap/FormControl';
import Dropdown from 'react-bootstrap/Dropdown';

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
    <a
        href=""
        ref={ref}
        onClick={(e) => {
            e.preventDefault();
            onClick(e);
        }}
    >
        {children}
  &#x25bc;
    </a>
));

// forwardRef again here!
// Dropdown needs access to the DOM of the Menu to measure it
const CustomMenu = React.forwardRef(
    ({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {
        const [value, setValue] = React.useState('');

        return (
            <div
                ref={ref}
                style={style}
                className={className}
                aria-labelledby={labeledBy}
            >
                <FormControl
                    autoFocus
                    className="mx-3 my-2 w-auto"
                    placeholder="Type to filter..."
                    onChange={(e) => setValue(e.target.value)}
                    value={value}
                />
                <ul className="list-unstyled">
                    {React.Children.toArray(children).filter(
                        (child) =>
                            !value || child.props.children.toLowerCase().startsWith(value),
                    )}
                </ul>
            </div>
        );
    },
);



export default function CustomDropdownComponent(props) {

    return <Dropdown>
        <Dropdown.Toggle as={CustomToggle} id="dropdown-custom-components">Список</Dropdown.Toggle>

        <Dropdown.Menu as={CustomMenu}>
            {!!props.dropDownItems && props.dropDownItems.map(item =>
                <Dropdown.Item key={item} eventKey={item} onSelect={(e) => props.selectedItem(e)}>{item}</Dropdown.Item>
            )}
            {/* <Dropdown.Item eventKey="3" active>Green</Dropdown.Item>
            <Dropdown.Item eventKey="1">Red</Dropdown.Item> */}
        </Dropdown.Menu>
    </Dropdown>

}